body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: rgba(0, 0, 0, .78);
  padding: 1rem;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
  display: grid;
}

.photo-card {
  border: 1px solid #fff;
  padding: .5rem;
}

.photo-card img {
  width: 100%;
  height: auto;
  max-height: 200px;
  object-fit: cover;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(8, 8, 8, .718);
  border: none;
  margin: 0 auto;
  padding: 1rem;
  display: block;
}

.load-more.hidden {
  display: none;
}

.search-form {
  justify-content: center;
  display: flex;
}

